.sidebar .topNav {
  position: relative;
  height: 60px;
  line-height: 60px;
  padding-left: 38px;
  z-index: 100;
  border-bottom: 1px solid #ebebeb;
  background-color: #fff;
  box-shadow: 0 1px 2px rgba(153, 153, 153, 0.05);
  min-width: 660px; }
  .sidebar .topNav a {
    font-size: 18px; }

.sidebar .content {
  width: 1240px;
  margin: 0 auto 40px; }
  .sidebar .content .left-stick {
    font-family: Politica;
    width: 80px;
    top: 80px;
    position: fixed;
    text-align: center;
    font-size: 12px; }
    .sidebar .content .left-stick > div {
      margin-bottom: 5px;
      position: relative; }
    .sidebar .content .left-stick .through span {
      display: inline-block;
      padding: 0 5px;
      background-color: #fff; }
    .sidebar .content .left-stick .through:after {
      content: "";
      display: block;
      width: 100%;
      height: 1px;
      background: #ccc;
      position: absolute;
      top: 50%;
      left: 0;
      z-index: -1; }
    .sidebar .content .left-stick .year {
      font-size: 20px; }
    .sidebar .content .left-stick .md {
      font-size: 34px;
      line-height: 1.2; }
    .sidebar .content .left-stick .time {
      font-size: 20px;
      margin-bottom: 25px; }
    .sidebar .content .left-stick .share, .sidebar .content .left-stick .comment {
      color: #808080;
      margin-top: 80px; }
    .sidebar .content .left-stick .share_bar a {
      width: 40px;
      height: 40px;
      line-height: 40px;
      cursor: pointer;
      display: block;
      margin: 10px auto 0;
      border-radius: 50%;
      -webkit-transition: all .5s;
      transition: all .5s; }
      .sidebar .content .left-stick .share_bar a .iconfont {
        font-size: 40px;
        display: inline-block;
        z-index: 2;
        position: relative;
        color: #ccc;
        -webkit-transition: all .5s;
        transition: all .5s; }
      .sidebar .content .left-stick .share_bar a:nth-child(1):hover .gh_wechat {
        color: #3dbe5b; }
      .sidebar .content .left-stick .share_bar a:nth-child(2):hover .gh_qq {
        color: #21b3f7; }
      .sidebar .content .left-stick .share_bar a:nth-child(3):hover .gh_pengyouquan {
        color: #f8c514; }
      .sidebar .content .left-stick .share_bar a:nth-child(4):hover .gh_sina {
        color: #ff5757; }
    .sidebar .content .left-stick .gh_comment {
      font-size: 30px;
      color: #ccc;
      cursor: pointer;
      -webkit-transition: all .5s;
      transition: all .5s; }
      .sidebar .content .left-stick .gh_comment:hover {
        color: #666; }
    .sidebar .content .left-stick:after {
      position: absolute;
      content: "";
      display: block;
      border-right: 1px solid #eee;
      width: 1px;
      height: 630px;
      top: 10px;
      right: -40px; }

